import "./input.css";
import React from 'react';

const Input = ({text,result}) => {
  return (
    <div className="input-wraper">
      <din className="result">
        <h2>{result}</h2>
      </din>
      <din className="text">
        <h4>{text}</h4>
      </din>
    </div>
  );
}

export default Input;
